import React from 'react';
import logoSvg from '../../assets/image/logoSvg.svg'

import styles from './LoginWindow.module.scss';
import { LoginWindowProps } from './LoginWindow.props';

export const LoginWindow = ({ status, setStatus }: LoginWindowProps): JSX.Element => {

	status ? document.body.style.overflowY = 'hidden' : document.body.style.overflowY = 'auto';

	const windowRef = React.useRef(null);
	const [loginWindow, setLoginWindow] = React.useState(true);
	const [regWindow, setRegWindow] = React.useState(false);
	const [passwordWindow, setPasswordWindow] = React.useState(false);
	const [newPasswordWindow, setNewPasswordWindow] = React.useState(false);

	React.useEffect(() => {
		document.body.addEventListener('click', clickOutSearch);
	}, []);

	const clickOutSearch = (e: any) => {
		if (windowRef.current === e.target) {
			closeWindow();
		}
	}

	const controlWindow = (setWindow: Function): any => {
		setStatus(false)
		setTimeout(() => {
			setLoginWindow(false);
			setRegWindow(false);
			setPasswordWindow(false);
			setNewPasswordWindow(false)

			setWindow(true);
		}, 700);
		setTimeout(() => { setStatus(true) }, 800)
	}

	const closeWindow = () => {
		setStatus(false);
	}


	return (
		<div>
			{regWindow
				?
				<div ref={windowRef} className={`${styles.login} ${status ? styles.active : ''}`}>
					<div className={styles.loginBody}>
						<div className={styles.loginLeft}>
							<h4 className={styles.loginLeftTitle}> !</h4>
							<p className={styles.loginButtonText}>  ? <button className={styles.loginSmallButton} onClick={() => controlWindow(setLoginWindow)}></button></p>
							<button className={styles.loginLeftButton} onClick={() => controlWindow(setLoginWindow)}></button>
						</div>
						<div className={`${styles.loginMain}`}>
							<div className={`${styles.loginContent} ${styles.register}`} >
								<img src={logoSvg} alt="" />
								<h3 className={styles.loginTitle}></h3>
								<p className={styles.loginText}>  ,        !</p>
								<form className={styles.loginForm} action="#" method="get">
									<input className={styles.loginInput} type="text" placeholder="'  " required />
									<input className={styles.loginInput} type="email" placeholder='Email' required />
									<input className={styles.loginInput} type="tel" placeholder='' required />
									<input className={styles.loginInput} type="password" placeholder=' ' required />
									<button className={`${styles.loginButton} button`}></button>
								</form>
							</div>
						</div>
						<button className={styles.loginClose} onClick={closeWindow}></button>
					</div>
				</div>
				: ''
			}
			{
				loginWindow
					?
					<div ref={windowRef} className={`${styles.login} ${status ? styles.active : ''}`}>
						<div className={`${styles.loginBody} ${styles.loginWindow}`}>
							<div className={styles.loginLeft}>
								<h4 className={styles.loginLeftTitle}></h4>
								<p className={styles.loginButtonText}>  !</p>
								<button className={styles.loginLeftButton} onClick={() => controlWindow(setRegWindow)}></button>
							</div>
							<div className={`${styles.loginMain}`}>
								<div className={`${styles.loginContent}`} >
									<img src={logoSvg} alt="" />
									<h3 className={styles.loginTitle}>   </h3>
									<form className={styles.loginForm} action="#" method="get">
										<input className={styles.loginInput} type="email" placeholder='Email' required />
										<input className={styles.loginInput} type="password" placeholder=' ' required />
										<button className={`${styles.loginButton} button`}></button>
										<div className={`${styles.loginButtons} ${styles.register}`}>
											<button className={styles.loginSmallButton} onClick={() => controlWindow(setRegWindow)}></button>
											<button className={styles.loginSmallButton} onClick={() => controlWindow(setPasswordWindow)}> ?</button>
										</div>
									</form>
								</div>
							</div>
							<button className={styles.loginClose} onClick={closeWindow}></button>
						</div>
					</div>
					: ''
			}
			{
				passwordWindow
					?
					<div ref={windowRef} className={`${styles.login} ${status ? styles.active : ''}`}>
						<div className={`${styles.loginBody} ${styles.loginWindow}`}>
							<div className={styles.loginLeft}>
								<h4 className={styles.loginLeftTitle}></h4>
								<p className={styles.loginButtonText}>  !</p>
								<button className={styles.loginLeftButton} onClick={() => controlWindow(setLoginWindow)}></button>
							</div>
							<div className={`${styles.loginMain}`}>
								<div className={`${styles.loginContent}`} >
									<img src={logoSvg} alt="" />
									<h3 className={styles.loginTitle}> ?</h3>
									<p className={styles.loginText}>          </p>
									<form className={styles.loginForm} action="#" method="get">
										<input className={styles.loginInput} type="email" placeholder='Email' required />
										<button className={`${styles.loginButton} button`} type='submit' onClick={() => controlWindow(setNewPasswordWindow)}> </button>
										<div className={styles.loginButtons}>
											<button className={styles.loginSmallButton} onClick={() => controlWindow(setLoginWindow)}>   </button>
										</div>
									</form>
								</div>
							</div>
							<button className={styles.loginClose} onClick={closeWindow}></button>
						</div>
					</div>
					: ''
			}
			{
				newPasswordWindow
					?
					<div ref={windowRef} className={`${styles.login} ${status ? styles.active : ''}`}>
						<div className={`${styles.loginBody} ${styles.loginWindow}`}>
							<div className={styles.loginLeft}>
								<h4 className={styles.loginLeftTitle}></h4>
								<p className={styles.loginButtonText}>  !</p>
								<button className={styles.loginLeftButton} onClick={() => controlWindow(setLoginWindow)}></button>
							</div>
							<div className={`${styles.loginMain}`}>
								<div className={`${styles.loginContent} ${styles.password}`} >
									<img src={logoSvg} alt="" />
									<h3 className={styles.loginTitle}> ?</h3>
									<p className={styles.loginText}>      .        </p>
									<form className={styles.loginForm} action="#" method="get">
										<input className={styles.loginInput} type="text" placeholder=' ' required />
										<input className={styles.loginInput} type="password" placeholder='  ' required />
										<button className={`${styles.loginButton} button`} type='submit' onClick={() => controlWindow(setLoginWindow)}> </button>
										<div className={`${styles.loginButtons} ${styles.cods}`}>
											<button className={styles.loginSmallButton} onClick={() => controlWindow(setLoginWindow)}>  </button>
										</div>
									</form>
								</div>
							</div>
							<button className={styles.loginClose} onClick={closeWindow}></button>
						</div>
					</div>
					: ''
			}
		</div >
	)
}

